<template>
  <div class="log-main">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button type="warning" @click="downloadLog" size="small"><i class="fa fa-download"></i> {{$t('common.download')}}</el-button>
    </div>
    <el-form :model="onlineForm" ref="onlineForm" label-width="85px" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="起始日期：" prop="startTime">
            <el-date-picker
              v-model="onlineForm.startTime"
              type="datetime"
              :picker-options="startDatePicker"
              :placeholder="$t('common.selectDate')">
            </el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="终止日期：" prop="endTime">
            <el-date-picker
              v-model="onlineForm.endTime"
              type="datetime"
              :picker-options="endDatePicker"
              :placeholder="$t('common.selectDate')">
            </el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="关键字：" prop="keyWord">
            <el-input v-model.trim="onlineForm.keyWord"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="日志级别：" prop="logLevel">
            <el-select class="block" v-model="onlineForm.logLevel" :placeholder="$t('common.select')">
              <el-option value="" label="请选择"></el-option>
              <el-option
                v-for="(item, index) in levelItems"
                :key="index"
                :label="item.text"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="索引：" prop="tranMode">
            <el-select class="block" v-model="onlineForm.tranMode">
              <el-option value="galaxy-online" label="galaxy-online"></el-option>
              <el-option v-for="item in onlineOption"
                :key="item.value"
                :label="item.value"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col v-for="item in dictTypeData" :key="item.id" :lg="6" :xl="4">
          <el-form-item :label="item.name + '：'" :prop="item.value" :label-width="(item.name.length + 2) + 'em'">
            <el-input :type="item.remarks" v-model.trim="onlineForm[item.value]"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4" class="mb-15">
          <el-button type="primary" @click="searchLog('search')" icon="el-icon-search" size="small">{{$t('common.search')}}</el-button>
          <el-button type="warning" @click="resetForm('onlineForm')" size="small"><i class="fa fa-refresh"></i> {{$t('common.reset')}}</el-button>
        </el-col>
      </el-row>
    </el-form>
    <gl-scroll-load :options="options" @scrollLoadData="scrollLoadData">
      <pre slot="main" v-html="logData.data"></pre>
      <div class="log-loading" slot="loading" v-loading="loading"
      element-loading-text="拼命加载中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0)"></div>
      <div class="load-last" v-if="currentPage === logData.totalPage" slot="footer">已经到最后一页了~</div>
    </gl-scroll-load>
  </div>
</template>

<script>
  import { mapState, mapActions } from 'vuex'

  export default {
    data() {
      return {
        options: {
          maxHeight: '450px'
        },
        loading: false,
        currentPage: 1,
        pageSize: 10,
        onlineForm: {
          startTime: '',
          endTime: '',
          keyWord: '',
          logLevel: '',
          tranMode: 'galaxy-online'
        },
        startDatePicker: this.beginDate(),
        endDatePicker: this.processDate(),
        paramsForm: {},
        levelItems: [
          {
            text: 'ERROR',
            value: 'ERROR'
          },
          {
            text: 'WARN',
            value: 'WARN'
          },
          {
            text: 'DEBUG',
            value: 'DEBUG'
          },
          {
            text: 'INFO',
            value: 'INFO'
          }
        ]
      }
    },
    computed: {
      ...mapState({
        logData: state => state.log.logData,
        dictTypeData: state => state.dict.dictTypeData,
        onlineOption: state => state.dict.onlineOption
      })
    },
    activated() {
      this.getTypeDict('onlineLog')
      this.getOnlineLogIndex('onlineIndex')
      this.searchLog()
    },
    deactivated() {
      this.currentPage = 1
    },
    methods: {
      ...mapActions('log', ['searchLogData', 'exportLog']),
      ...mapActions('dict', ['getTypeDict', 'getOnlineLogIndex']),
      beginDate() {
        let vm = this
        return {
          disabledDate(time) {
            if (vm.onlineForm.endTime) {
              return time.getTime() > new Date(vm.onlineForm.endTime).getTime()
            } else {
              return time.getTime() > Date.now()// 开始时间不选时，结束时间最大值小于等于当天
            }
          }
        }
      },
      // 提出结束时间必须大于提出开始时间
      processDate() {
        let vm = this
        return {
          disabledDate(time) {
            // console.log(time)
            if (vm.onlineForm.startTime) {
              return time.getTime() < new Date(vm.onlineForm.startTime).getTime() || time.getTime() > Date.now()
            } else {
              return time.getTime() > Date.now()// 开始时间不选时，结束时间最大值小于等于当天
            }
          }
        }
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
      },
      handleSizeChange(val) {
        const vm = this
        vm.pageSize = val
        vm.searchLog()
      },
      handleCurrentChange(val) {
        const vm = this
        vm.currentPage = val
        vm.searchLog()
      },
      downloadLog() {
        const vm = this
        vm.exportLog(vm)
      },
      // 查询
      searchLog(search) {
        const vm = this
        if (search) {
          vm.currentPage = 1
        }
        vm.paramsForm = Object.assign({
          pageNo: vm.currentPage,
          pageSize: vm.pageSize
        }, vm.onlineForm)

        vm.searchLogData(vm)
      },
      scrollLoadData() {
        const vm = this
        if (!vm.loading && vm.currentPage < vm.logData.totalPage) {
          vm.currentPage++
          vm.searchLog()
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/common.scss";
</style>



